"use client";

import React, { useState, useEffect } from "react";
import { useParams } from "next/navigation";

interface Product {
  id: string;
  name: string;
  description: string;
  category: string;
  size: string;
  price: number;
  imagePreview: string | null;
}

interface CartItem extends Product {
  quantity: number;
  comments: string;
}

export default function CommercePage() {
  const params = useParams();
  const commerce = params?.commerce as string;

  // Mock products data - in real app, fetch from API by commerce
  const [products, setProducts] = useState<Product[]>([
    {
      id: "1",
      name: "Producto 1",
      description: "Descripción del producto 1",
      category: "Categoría A",
      size: "Mediano",
      price: 10.0,
      imagePreview: null,
    },
    {
      id: "2",
      name: "Producto 2",
      description: "Descripción del producto 2",
      category: "Categoría B",
      size: "Grande",
      price: 15.5,
      imagePreview: null,
    },
  ]);

  const [cart, setCart] = useState<CartItem[]>([]);
  const [customerName, setCustomerName] = useState("");
  const [paymentAmount, setPaymentAmount] = useState("");
  const [change, setChange] = useState<number | null>(null);

  // Load cart from localStorage
  useEffect(() => {
    const savedCart = localStorage.getItem(`cart_${commerce}`);
    if (savedCart) {
      setCart(JSON.parse(savedCart));
    }
  }, [commerce]);

  // Save cart to localStorage
  useEffect(() => {
    localStorage.setItem(`cart_${commerce}`, JSON.stringify(cart));
  }, [cart, commerce]);

  function addToCart(product: Product) {
    setCart((prev) => {
      const existing = prev.find((item) => item.id === product.id);
      if (existing) {
        return prev.map((item) =>
          item.id === product.id
            ? { ...item, quantity: item.quantity + 1 }
            : item
        );
      } else {
        return [...prev, { ...product, quantity: 1, comments: "" }];
      }
    });
  }

  function removeFromCart(productId: string) {
    setCart((prev) => prev.filter((item) => item.id !== productId));
  }

  function updateQuantity(productId: string, quantity: number) {
    if (quantity < 1) return;
    setCart((prev) =>
      prev.map((item) =>
        item.id === productId ? { ...item, quantity } : item
      )
    );
  }

  function updateComments(productId: string, comments: string) {
    setCart((prev) =>
      prev.map((item) =>
        item.id === productId ? { ...item, comments } : item
      )
    );
  }

  function calculateSubtotal() {
    return cart.reduce((sum, item) => sum + item.price * item.quantity, 0);
  }

  function handlePaymentChange(e: React.ChangeEvent<HTMLInputElement>) {
    setPaymentAmount(e.target.value);
    const payment = parseFloat(e.target.value);
    if (!isNaN(payment)) {
      const subtotal = calculateSubtotal();
      setChange(payment - subtotal);
    } else {
      setChange(null);
    }
  }

  function generateWhatsAppMessage() {
    if (!customerName.trim()) {
      alert("Por favor ingresa tu nombre.");
      return;
    }
    if (cart.length === 0) {
      alert("El carrito está vacío.");
      return;
    }
    if (!paymentAmount || isNaN(parseFloat(paymentAmount))) {
      alert("Por favor ingresa un monto válido de pago.");
      return;
    }

    let message = `Hola, mi nombre es ${customerName}, hice un pedido en la plataforma de planbdelivery. Te pido:\n\n`;

    cart.forEach((item) => {
      message += `- ${item.quantity} x ${item.name} (${item.size}) - $${item.price.toFixed(2)}\n  Comentarios: ${item.comments || "Ninguno"}\n`;
    });

    const subtotal = calculateSubtotal();
    message += `\nSubtotal: $${subtotal.toFixed(2)}\n`;
    message += `Monto abonado: $${parseFloat(paymentAmount).toFixed(2)}\n`;
    if (change !== null) {
      message += `Vuelto: $${change.toFixed(2)}\n`;
    }

    const phoneNumber = "1234567890"; // Replace with commerce phone number from API
    const encodedMessage = encodeURIComponent(message);
    const whatsappUrl = `https://wa.me/${phoneNumber}?text=${encodedMessage}`;
    window.open(whatsappUrl, "_blank");
  }

  return (
    <div className="min-h-screen bg-white text-black font-sans p-6 max-w-7xl mx-auto">
      <h1 className="text-3xl font-bold mb-6">Comercio: {commerce}</h1>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <div>
          <h2 className="text-2xl font-semibold mb-4">Productos</h2>
          {products.length === 0 && <p>No hay productos disponibles.</p>}
          <ul className="space-y-4">
            {products.map((product) => (
              <li
                key={product.id}
                className="border border-gray-300 rounded p-4 flex flex-col"
              >
                {product.imagePreview && (
                  <img
                    src={product.imagePreview}
                    alt={product.name}
                    className="w-full h-40 object-cover rounded mb-2"
                  />
                )}
                <h3 className="font-semibold">{product.name}</h3>
                <p className="text-sm text-gray-600">{product.description}</p>
                <p className="text-sm">
                  Categoría: {product.category} | Tamaño: {product.size}
                </p>
                <p className="text-lg font-semibold mt-2">
                  ${product.price.toFixed(2)}
                </p>
                <button
                  onClick={() => addToCart(product)}
                  className="mt-3 bg-black text-white px-4 py-2 rounded hover:bg-gray-800 transition"
                >
                  Agregar al carrito
                </button>
              </li>
            ))}
          </ul>
        </div>
        <div className="md:col-span-2">
          <h2 className="text-2xl font-semibold mb-4">Carrito de Compras</h2>
          {cart.length === 0 && <p>El carrito está vacío.</p>}
          {cart.length > 0 && (
            <div className="space-y-4">
              {cart.map((item) => (
                <div
                  key={item.id}
                  className="border border-gray-300 rounded p-4 flex flex-col"
                >
                  <div className="flex justify-between items-center mb-2">
                    <h3 className="font-semibold">{item.name}</h3>
                    <button
                      onClick={() => removeFromCart(item.id)}
                      className="text-red-600 hover:underline"
                    >
                      Eliminar
                    </button>
                  </div>
                  <p className="text-sm text-gray-600">{item.description}</p>
                  <p className="text-sm">
                    Categoría: {item.category} | Tamaño: {item.size}
                  </p>
                  <p className="text-lg font-semibold mt-2">
                    ${item.price.toFixed(2)} x{" "}
                    <input
                      type="number"
                      min="1"
                      value={item.quantity}
                      onChange={(e) =>
                        updateQuantity(item.id, parseInt(e.target.value))
                      }
                      className="w-16 border border-gray-300 rounded px-2 py-1 text-center"
                    />
                  </p>
                  <textarea
                    placeholder="Comentarios sobre el producto"
                    value={item.comments}
                    onChange={(e) => updateComments(item.id, e.target.value)}
                    className="w-full border border-gray-300 rounded px-3 py-2 mt-2 resize-none"
                    rows={2}
                  />
                </div>
              ))}
              <div className="mt-4">
                <p className="text-lg font-semibold">
                  Subtotal: ${calculateSubtotal().toFixed(2)}
                </p>
                <input
                  type="text"
                  placeholder="Tu nombre"
                  value={customerName}
                  onChange={(e) => setCustomerName(e.target.value)}
                  className="w-64 border border-gray-300 rounded px-3 py-2 mt-4"
                />
                <input
                  type="number"
                  placeholder="Monto abonado"
                  value={paymentAmount}
                  onChange={handlePaymentChange}
                  className="w-40 border border-gray-300 rounded px-3 py-2 mt-2"
                  min="0"
                  step="0.01"
                />
                {change !== null && (
                  <p className="mt-2">
                    Vuelto: <span className="font-semibold">${change.toFixed(2)}</span>
                  </p>
                )}
                <button
                  onClick={generateWhatsAppMessage}
                  className="mt-4 bg-black text-white px-6 py-3 rounded hover:bg-gray-800 transition w-full"
                >
                  Pedir Ahora
                </button>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
